<template>
  <div id="app">
    
    <!-- 商城公告和商家入驻口 -->
    <div class="news_sj_dxs_box bgwh mb6">
    	<div class="scrollnews">
    		<span>
                <router-link to="newslist">
                    <img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/news_tit.png">
                </router-link>
            </span>
    		<ul>
    			<li v-for="(item,index) in gonggao" :key="index">
                    <router-link :to="item.url">{{item.txt}}</router-link>
                </li>
    		</ul>
    	</div>
    	<div class="sj_dxs_box">
    		<h2>
    			<router-link to="applyindex">
    				<img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/bg_sj.jpg">
    			</router-link>
    		</h2>
    	</div>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import $ from 'jquery'
export default {
  name: 'app',
  data () {
    return {
        gonggao:[{
            url:'newsxq',
            txt:'热烈祝贺颜文明体育贵阳市枣山路店盛大开业'
        },{
            url:'newsxq',
            txt:'认真学习十九大精神，争取贯彻落实习总书记讲话'
        },{
            url:'newsxq',
            txt:'董事长在员工大会上发表演讲！'
        },],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
       //公告的swiper
       
         /*
            滚动的文字公告
            3秒换一句
            */ 
                    setInterval(function (){
                        $(".scrollnews ul").animate({marginTop:"-.4rem"},300,
                            function(){
                                $(".scrollnews ul li:last").after($(".scrollnews ul li:first"))
                                $(".scrollnews ul").css("margin-top",0)
                            }
                        )
                    },3000)
      
  }
}
</script>

<style>
</style>



