	<div class="zy_supplier_box zy_supplier_jiesbox">
		<div class="name mb6">
			<h3 class="_picbox_w3_h2">
				<i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo1.jpg"></i>
				<dl>
					<dt>颜文明体育用品</dt>
					<dd>高级店铺</dd>
				</dl>
			</h3>
			<span onclick="javascript:alert('收藏成功')"><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/icon_like.png"></i>收藏</span>
		</div>
		<div class="txt dis_flex mb6">
			<router-link to="supplier"><em><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/icon_goods_dp_w.png" /></em>首页</router-link>
			<router-link to="supplier_goods"><em>23</em>全部商品</router-link>
			<span>描述：<em>5.0</em></span>
			<span>服务：<em>5.0</em></span>
			<span>物流：<em>5.0</em></span>
		</div>
		<ul class="mb6 bgwh">
			<li>
				<span>好评率</span>
				<router-link to="">100%</router-link>
			</li>
			<li>
				<span>所在地</span>
				<router-link to="">贵州·贵阳·清镇市</router-link>
			</li>
			<li>
				<span>详细地址</span>
				<router-link to="">清镇市巢东路9号颜文明大厦</router-link>
			</li>
			<li>
				<span>开店时间</span>
				<router-link to="">2018-07-27</router-link>
			</li>
			<li class="erwm">
				<span>店铺二维码</span>
				<em><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/mb_wangid.png"><i>扫描商家二维码，关注送大礼</i></em>
			</li>
