<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

import totop from '../index/totop.vue'
import heanr from './heanr.vue'
import buttom from './buttom.vue'
import sousuo from './sousuo.vue'


export default {
	components:{totop,heanr,buttom,sousuo},

data () {
    return {
              
 
		 

}
  },
  mounted(){
           
	 	$(function(){
		$(".zy_search_top .box").mousedown(
			function(){
				$(".zy_search_bomb_box").show()
				$(".zy_search_top_box").hide()			
				$(".zy_module-content").hide()
			}
		)
		$(".zy_search_bomb_box .fix>a").mousedown(
			function(){
				$(".zy_search_bomb_box").hide()
				$(".zy_search_top_box").show()		
				$(".zy_module-content").show()
			}
		)
	})       

  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_main.css');


</style>



