<template>
  <div id="app">
    
<div class="zy_login_box">
	<header class="zy_search_top_box">
		<div class="zy_title_top fix">
			<div class="one" href="javascript:" onclick="history.back();"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png"></div>
			<h1>会员登录</h1>
			<router-link to="reg">免费注册</router-link>
		</div>
	</header>	

	<!-- 内容框 -->
	<div class="zy_module-content">
		<div class="login_box">
			<div class="logo"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/logo02.png"></div>
			<form>
				<div>
					<label><router-link to=""><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_user.png"></router-link><input type="text" name="" placeholder="手机号/用户名/邮箱"></label>
				</div>
				<div>
					<label><router-link to=""><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_mm.png"></router-link><input type="password" name="" placeholder="请输入密码"></label>
				</div>
				<button class="zy_login_btn">登 录</button>
				<h2>
					<label><input type="checkbox" name="">自动登录</label>
					<router-link to="findpwd">忘记密码？点此找回</router-link>
				</h2>
			</form>
		</div>		
	</div>
</div>
    <buttom_nav></buttom_nav>
  </div>
</template>

<script>
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'




import buttom_nav from '../index/buttom_nav.vue'

export default {
  components:{buttom_nav},
  data () {
    return {
    }
  }
}
</script>

<style scoped>

@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');

@import url('../../assets/css/news_login_apply.css');




</style>



