import VueRouter from 'vue-router'
// 引入组件
import index from './components/index.vue'
import fenlei from './components/fenlei.vue'
import gouwuche from './components/gouwuche.vue'
import my from './components/user.vue'
//登录注册
import login from './components/login/login.vue'
import reg from './components/login/reg.vue'
import findpwd from './components/login/findpwd.vue'
//商品列表
import category from './components/category/category.vue'
import category_list from './components/category/category_list.vue'
//商城公告
import newslist from './components/news/newslist.vue'
import newsxq from './components/news/newsxq.vue'
//商家入驻
import applyindex from './components/apply/apply_index.vue'
import apply from './components/apply/apply.vue'
import apply01 from './components/apply/apply01.vue'
import apply02 from './components/apply/apply02.vue'
import apply03 from './components/apply/apply03.vue'
//商品详情页
import goods from './components/goods/goods.vue'
//底部几个链接
import supplier_privilege  from './components/buttom/supplier-privilege.vue'
import about  from './components/about/about.vue'
import about_mdcx  from './components/about/about_mdcx.vue'
//会员中心的设置的三个页面
import user_info from './components/user/user_info.vue'
import user_info_mima from './components/user/user_info_mima.vue'
import user_info_person from './components/user/user_info_person.vue'
//收藏：：
import user_shouc from './components/user/user_shouc.vue'
import user_shouc_dp from './components/user/user_shouc_dp.vue'
//我的订单
import user_order from './components/user/user_order.vue'
import user_order_detail from './components/user/user_order_detail.vue'
import user_order_kuaidi from './components/user/user_order_kuaidi.vue'
//我的评价
import user_comment from './components/user/user_comment.vue'
import user_comment_pinj from './components/user/user_comment_pinj.vue'
import user_comment_shaid from './components/user/user_comment_shaid.vue'

//我的钱包
import user_account_detail from './components/user/user_account_detail.vue'
//在线充值
import user_recharge from './components/user/user_recharge.vue'
import user_recharge_know from './components/user/user_recharge_know.vue'
//我的优惠券
import user_packet from './components/user/user_packet.vue'
//我的留言
import user_msg from './components/user/user_msg.vue'

//地址管理
import user_addr from './components/user/user_addr.vue'
import user_addr_dizhi from './components/user/user_addr_dizhi.vue'
import user_addr_new from './components/user/user_addr_new.vue'

//退款/售后
import user_back_order from './components/user/user_back_order.vue' 
import user_back_order_ck from './components/user/user_back_order_ck.vue' 
import user_back_order_tk from './components/user/user_back_order_tk.vue' 

//店铺
import supplier from './components/supplier/supplier.vue' 
import supplier_goods from './components/supplier/supplier_goods.vue' 
import supplier_catalog from './components/supplier/supplier_catalog.vue' 
import supplier_introduce from './components/supplier/supplier_introduce.vue' 


































export default new VueRouter({
    // 配置路由
    routes: [{ 
        path: '/',
        component: index
    },{ 
        path: '/index',
        component: index
    },{ 
        path: '/fenlei',
        component: fenlei
    },{ 
        path: '/gouwuche',
        component: gouwuche
    },{ 
        path: '/my',
        component: my
    },{ //登良路
        path: '/login',
        component: login
    },{ 
        path: '/reg',
        component: reg
    },{ 
        path: '/findpwd',
        component: findpwd
    },{ //商品列表
        path: '/category',
        component: category
    },{ //
        path: '/category_list',
        component: category_list
    },{ //商城公告
        path: '/newslist',
        component: newslist
    },{ //公告详情
        path: '/newsxq',
        component: newsxq
    },{ //商家入驻
        path: '/applyindex',
        component: applyindex
    },{ //
        path: '/apply',
        component: apply
    },{ //商家入驻001
        path: '/apply01',
        component: apply01
    },{ //商家入驻002
        path: '/apply02',
        component: apply02
    },{ //商家入驻03
        path: '/apply03',
        component: apply03
    },{ //商品详情页
        path: '/goods',
        component: goods
    },{ //底部几个链接
        path: '/supplier_privilege',
        component: supplier_privilege
    },{ //
        path: '/about',
        component: about
    },{ //
        path: '/about_mdcx',
        component: about_mdcx
    },{ //会员中心的三个设置页面
        path: '/user_info',
        component: user_info
    },{ //会员中心的三个设置页面
        path: '/user_info_mima',
        component: user_info_mima
    },{ //会员中心的三个设置页面
        path: '/user_info_person',
        component: user_info_person
    },{ //收藏物品
        path: '/user_shouc',
        component: user_shouc
    },{ //收藏店铺
        path: '/user_shouc_dp',
        component: user_shouc_dp
    },{ //我的订单
        path: '/user_order',
        component: user_order
    },{ //
        path: '/user_order_detail',
        component: user_order_detail
    },{ //
        path: '/user_order_kuaidi',
        component: user_order_kuaidi
    },{ //我的评价
        path: '/user_comment',
        component: user_comment
    },{ //
        path: '/user_comment_pinj',
        component: user_comment_pinj
    },{ //
        path: '/user_comment_shaid',
        component: user_comment_shaid
    },{ //我的钱包
        path: '/user_account_detail',
        component: user_account_detail
    },{ //在线充值
        path: '/user_recharge',
        component: user_recharge
    },{ //
        path: '/user_recharge_know',
        component: user_recharge_know
    },{ //我的优惠券
        path: '/user_packet',
        component: user_packet
    },{ //我的留言
        path: '/user_msg',
        component: user_msg
    },{ //地址管理
        path: '/user_addr',
        component: user_addr
    },{ //
        path: '/user_addr_dizhi',
        component: user_addr_dizhi
    },{ //
        path: '/user_addr_new',
        component: user_addr_new
    },{ //退款/售后
        path: '/user_back_order',
        component: user_back_order
    },{ //
        path: '/user_back_order_ck',
        component: user_back_order_ck
    },{ //
        path: '/user_back_order_tk',
        component: user_back_order_tk
    },{ //店铺
        path: '/supplier',
        component: supplier
    },{ //店铺商品
        path: '/supplier_goods',
        component: supplier_goods
    },{ //
        path: '/supplier_catalog',
        component: supplier_catalog
    },{ //
        path: '/supplier_introduce',
        component: supplier_introduce
    },
    
    
    
    
    







]
})


