<template>
  <div id="app">


      
<top :txt="txt"></top>







<div class="top_zhanwei_box"></div>	 
<!-- 内容框 -->
<div class="zy_module-content bgwh">	
	<div class="user_comment_shaid p_02">
		<form>
			<div class="vip_comment_goods">
				<i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/big_5.jpg"></i>
				<dl>
					<dt>2018年颜文明旗舰店新款白色专业网球女子训练跑步鞋男休闲鞋正品</dt>
					<dd><router-link to="">￥105.00</router-link><span>￥159.00</span></dd>
