<template>
  <div id="app">

      <header class="zy_search_top_box">
	<div class="zy_search_top fix">
		<div class="one" href="javascript:" onclick="history.back();">
            <img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png">
            </div>
		<div class="box">
			<span>
                <img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/icon_search.png"/>
                </span>
		    <input type="text" placeholder="新品上市" value="" />
	    </div>
	    <div class="r">
	    	<span>分类</span>
	    	<dl>
	    		<dt></dt>
	    		<dd><router-link to="">分类</router-link></dd>
	    		<dd><router-link to="">品牌</router-link></dd>
	    		<dd><router-link to="">店铺</router-link></dd>
	    	</dl>
	    </div>
	   
	</div>
</header>
    
  </div>
</template>

<script>
import $ from 'jquery'
export default {

data () {
    return {

}
  },
  mounted(){
$(function(){
				$(".zy_search_top .r span").mousedown(
					function(event){
						$(".zy_search_top .r dl").fadeToggle()
						event.stopPropagation();//阻止冒泡
					}
				)
				$("html,body").mousedown(
					function(){
						$(".zy_search_top .r dl").fadeOut()
					}
				)
			})
  }
}
</script>

<style>
</style>



