<template>
  <div id="app">

   <!-- 头部 -->
   <heanr></heanr>

  <!-- 内容框 -->
<div class="zy_module-content">


<!-- 左边导航图 -->

<lefts></lefts>













  <!-- 底部导航 -->
    <buttom_nav></buttom_nav>

</div>



    
  
    <!-- 搜索框 -->
<sousuo></sousuo>

  </div>
</template>

<script>
 import Swiper from 'swiper'
 import $ from 'jquery'


import heanr from './fenlei/heanr.vue'
import lefts from './fenlei/lefts.vue'



import buttom_nav from './index/buttom_nav.vue'
import sousuo from './index/sousuo.vue'
export default {

components:{
  buttom_nav,
  heanr,
  lefts,
  sousuo
  },

  data () {
    return {

}
  },mounted(){
    $(function(){
		$(".zy_search_top .box").mousedown(
			function(){
				$(".search_bomb_box").show()
				$(".zy_search_top_box").hide()			
				$(".zy_module-content").hide()
			}
		)
		$(".search_bomb_box .search_top>a").mousedown(
			function(){
				$(".search_bomb_box").hide()
				$(".zy_search_top_box").show()		
				$(".zy_module-content").show()
			}
		)
	})
  }
}
</script>

<style scoped>
@import url('../assets/css/index.css');
@import url('../assets/css/public/public_lib.css');
@import url('../assets/css/zy_public.css');
@import url('../assets/css/catalog.css');



</style>



