<template>
  <div id="app">

      <header class="zy_search_top_box vip_top_box">
	<div class="zy_title_top fix">
		<div class="one" href="javascript:" onclick="history.back();"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png"></div>
		<h1>{{txt}}</h1>
	    <div class="r">
	    	<span><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_menu.png"></span>
	    	<dl>
	    		<dt></dt>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon01.png"></i><router-link to="index">首页</router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon02.png"></i><router-link to="feilei">分类</router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon03.png"></i><router-link to="gouwuche">购物车</router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon04.png"></i><router-link to="my">会员中心</router-link></dd>
	    	</dl>
	    </div>
	</div>
</header>
      
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

export default {
props:['txt'],
data () {
    return {

}
  },
  mounted(){
      $(function(){
				// $(".zy_title_top .r dl").hide()
				$(".zy_title_top .r span").mousedown(
					function(event){
						$(".zy_title_top .r dl").fadeToggle()
						event.stopPropagation();//阻止冒泡
					}
				)
				$("html,body").mousedown(
					function(){
						$(".zy_title_top .r dl").fadeOut()
					}
				)
			})
  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');


</style>



