<template>
  <div id="app">
  
      

      





<heanr></heanr>
 

<!-- 内容框 -->
<div class="zy_module-content">
	<div class="zy_supplier_fenlbox">
		<h2><router-link  to="supplier_goods">全部商品</router-link><em></em></h2>
		<ul>
			<li>
				<h2><router-link to="">经典鞋</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods">经典男鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">经典女鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">经典网球鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">经典跑步鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">经典女鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">经典网球鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">经典跑步鞋</router-link> ></dd>
				</dl>
			</li>
			<li>
				<h2><router-link to="">男鞋</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods">篮球鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">休闲鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">运动鞋</router-link> ></dd>
				</dl>
			</li>
			<li>
				<h2><router-link to="">女鞋</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods.html">休闲鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods.html">跑步鞋</router-link> ></dd>
				</dl>
			</li>
			<li>
				<h2><router-link to="">男装</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods">运动衣</router-link> ></dd>
				</dl>
			</li>
			<li>
				<h2><router-link to="">女装</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods">运动衣</router-link> ></dd>
					<dd><router-link to="supplier_goods">T恤</router-link> ></dd>
				</dl>
			</li>
			<li>
				<h2><router-link to="">儿童</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods">运动鞋</router-link> ></dd>
					<dd><router-link to="supplier_goods">套装</router-link> ></dd>
				</dl>
			</li>
			<li>
				<h2><router-link to="">配件</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods">护膝</router-link> ></dd>
					<dd><router-link to="supplier_goods">运动帽</router-link> ></dd>
				</dl>
			</li>
			<li>
				<h2><router-link to="">体育用品</router-link><em></em></h2>
				<dl>
					<dd><router-link to="supplier_goods">篮球</router-link> ></dd>
					<dd><router-link to="supplier_goods">网球</router-link> ></dd>
				</dl>
			</li>
		</ul>
		 
	</div>
	
	
	<!-- 返回顶部 -->
	<totop></totop>

	<!-- 底部导航 -->
	 <buttom></buttom>
	
</div>

<!-- 搜索框 -->
 <sousuo></sousuo>
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

import totop from '../index/totop.vue'
import heanr from './heanr.vue'
import buttom from './buttom.vue'
import sousuo from './sousuo.vue'


export default {
	components:{totop,heanr,buttom,sousuo},

data () {
    return {
              
 
		 

}
  },
  mounted(){
 
          	$(function(){
		$(".zy_search_top .box").mousedown(
			function(){
				$(".zy_search_bomb_box").show()
				$(".zy_search_top_box").hide()			
				$(".zy_module-content").hide()
			}
		)
		$(".zy_search_bomb_box .fix>a").mousedown(
			function(){
				$(".zy_search_bomb_box").hide()
				$(".zy_search_top_box").show()		
				$(".zy_module-content").show()
			}
		)
	})
           
	        $(function(){
				$(".zy_supplier_fenlbox ul li").mousedown(
					function(){
						$(this).find("dl").slideToggle()
						if ($(this).find("h2 em").hasClass("xz")) {
							$(this).find("h2 em").removeClass("xz")
							$(this).find("h2 em").addClass("xz01")
							$(this).find("h2").hasClass("zy_dpfl_cur")
							$(this).find("h2").removeClass("zy_dpfl_cur")
						}else{
							$(this).find("h2 em").addClass("xz")
							$(this).find("h2 em").removeClass("xz01")
							$(this).find("h2").addClass("zy_dpfl_cur")
						}
					}
				)
			})

  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_main.css');


</style>



