      <header class="zy_search_top_box sjrz_top_box">
	<div class="zy_title_top fix">
		<div class="one" href="javascript:" onclick="history.back();"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png"></div>
		<h1>商家入驻</h1>
		<div class="r">
	    	<span><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_menu.png"></span>
	    	<dl>
	    		<dt></dt>
	    		<dd><router-link to="">商家申请入驻</router-link></dd>
	    		<dd><router-link to="">商家管理中心</router-link></dd>
	    		<dd><router-link to="">商家帮助中心</router-link></dd>
	    	</dl>
	    </div>
        
	</div>
</header>	


<!-- 内容框 -->
<div class="zy_module-content">
	<div class="zy_ershou_box zy_apply_index">
