			<ul class="swiper-wrapper">
				<li class="swiper-slide">
					<img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/sjrz_banner.jpg">
				</li>
				<li class="swiper-slide">
					<img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/sjrz_banner.jpg">
				</li>
				<li class="swiper-slide">
					<img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/sjrz_banner.jpg">
				</li>
			</ul>
			<div class="swiper-pagination zy_sp"></div>
			
	    </div>
	    <div class="ersh_login bgwh">
	    	<p>您好，请先登录！</p>
	    	<h2><router-link to="apply">我要入驻</router-link>
            <router-link to="">查看入驻进度</router-link></h2>
	    </div>
		<!-- 流程入驻 -->
	    <div class="ruzlc bgwh mb6 p_2">
	    	<div class="zy_title_con">
				<h2><span>入驻流程<i></i></span></h2>
			</div>
			<ul class="one">
				<li><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_sjlc01.png"><router-link to="">签署入驻协议</router-link><em></em></li>
				<li><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_sjlc02.png"><router-link to="">商家申请入驻</router-link><em></em></li>
				<li><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_sjlc03.png"><router-link to="">平台审核资质</router-link><em></em></li>
			</ul>
			<ul class="one">
				<li><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_sjlc04.png"><router-link to="">商家缴纳费用</router-link><em></em></li>
				<li><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_sjlc05.png"><router-link to="">店铺开通使用</router-link><em></em></li>
			</ul>
	    </div>

    	<div id="leftTabBox" class="ersh_tab bgwh">
    		<div class="zy_title_con">
				<h2><span>入驻指南<i></i></span></h2>
			</div>
			<div class="hd">
				<ul>
					<li :class="{'on':ons=='yi'}" @click="ons='yi'"><router-link to="">招商方向</router-link></li>
					<li :class="{'on':ons=='er'}" @click="ons='er'"><router-link to="">招商标准</router-link></li>
					<li :class="{'on':ons=='san'}" @click="ons='san'"><router-link to="">资质要求</router-link></li>
					<li :class="{'on':ons=='si'}" @click="ons='si'"><router-link to="">资费标准</router-link></li>
				</ul>
			</div>
			<div class="bd">
				<div v-if="ons=='yi'">
					<p>2018开发平台招商方向</p>
					<p>1.品牌<br/>
					国际国内知名品牌<br/>
					开放平台将一如既往的最大程度地维护卖家的品牌利益，尊重品牌传统和内涵，欢迎优质品牌旗 舰店入驻，请参见《2017年开放平台重点招募品牌》。 
					</p>
					<p>2.货品<br/>
					能够满足用户群体优质、有特色的货品。<br/>
					根据类目结构细分的货品配置。类目规划详见《2017年开放平台类目一览表》。  
					</p>
					<p>3. 垂直电商<br/>
					开放平台欢迎垂直类电商入驻。开放平台愿意和专业的垂直电商企业分享其优质用户群体， 并且欢迎垂直电商为用户提供该领域专业的货品及服务。  
					</p>
				</div>
				<div v-if="ons=='er'"><h1 style="height:400px">招标标准</h1></div>
				<div v-if="ons=='san'"><h1 style="height:400px">资质要求</h1></div>
				<div v-if="ons=='si'"><h1 style="height:400px">资费标准</h1></div>
			</div>
		</div>
	</div>
	
	
</div>


    <buttom></buttom>
    <buttom_nav></buttom_nav>
  </div>
</template>

<script>
import Swiper from 'swiper'
import $ from 'jquery'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'


import buttom from '../index/buttom.vue'
import buttom_nav from '../index/buttom_nav.vue'
export default {
    components:{buttom_nav,buttom},

data () {
    return {
        ons:'yi'

}
  },
  mounted(){
      $(function(){
				// $(".zy_title_top .r dl").hide()
				$(".zy_title_top .r span").mousedown(
					function(event){
						$(".zy_title_top .r dl").fadeToggle()
						event.stopPropagation();//阻止冒泡
					}
				)
				$("html,body").mousedown(
					function(){
						$(".zy_title_top .r dl").fadeOut()
					}
				)
            });
            new Swiper('.zy_banner_ersh', {
			        pagination: {
                        el:'.zy_sp'
                    },
			        paginationClickable: true,
			        autoplay: {
                delay: 2000,
                    },
			        speed:500,
			        autoplayDisableOnInteraction : false,
			    });

  }
}
</script>

<style scoped>

@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');

@import url('../../assets/css/news_login_apply.css');

.ersh_tab .hd ul a{
    color: #666;
}
.ersh_tab .hd ul .on a{
    color: #333;
}


</style>



