<template>
  <div id="app">


      
<top :txt="txt"></top>
 
<div class="top_zhanwei_box"></div>	
 

<!-- 内容框 -->
<div class="zy_module-content">	
	<div class="vip_user_addr_new vip_user_addr_dizhi">
		<div class="box dizhi_new">
			<form>
				<div class="txt">
					<h2><input type="text" name="" value="买了个东西"></h2>
					<h2><input type="text" name="" placeholder="电子邮件地址"></h2>
					<div class="pick-area pick-area1" name="贵州省/贵阳市/云岩区"></div>
					<h2><input type="text" name="" value="下合群路达亨大厦16-6"></h2>
					<h2><input type="text" name="" value="15085208888"></h2>
					<h2><input type="text" name="" placeholder="邮政编码"></h2>
				</div>
				<div class="dizhi_foot dis_flex">
					<a>删除</a>
					<h2>
						<span>设为默认收货地址</span>
						<input type="submit" name="" value="保存更改">
					</h2>
				</div>
			</form>
		</div>

		 
	</div>
</div>
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

import '../../assets/js/pick-pcc.min.1.0.2.js'

import top from '../index/top.vue'
export default {
	components:{top},

data () {
    return {
			txt:'地址管理'

}
  },
  mounted(){
     $(function(){
		var top_h=$(".vip_top_box").height()
		$(".top_zhanwei_box").css("height",top_h)
    });
    $(".pick-area1").pickArea({
		    	"display":"block",
		    	"hoverColor":"#999",//背景颜色
		    	"fontWeight":"normal",
		        // "width":"420",//设置“省市县”文本边框的宽度
		        // "height":"48",//设置“省市县”文本边框的高度
		        "maxHeight":"300px",
		    })
           

  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_vip.css');


</style>



