<template>
  <div id="app">


      
<top :txt="txt"></top>
 
<div class="top_zhanwei_box"></div>	
 
<!-- 内容框 -->
<div class="zy_module-content bgwh">	
	<div class="user_comment_pinj p_02">
		<form>
			<div class="vip_comment_goods">
				<i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/big_5.jpg"></i>
				<dl>
					<dt>2018年颜文明旗舰店新款白色专业网球女子训练跑步鞋男休闲鞋正品</dt>
					<dd><router-link to="">￥205.00</router-link><span>￥259.00</span></dd>
				</dl>
			</div>
			<div class="pingfen">
				<h2>评分</h2>
				<dl>
					<dd><b>描述：</b><span><i></i><i></i><i></i><i></i><i></i></span></dd>
					<dd><b>服务：</b><span><i></i><i></i><i></i><i></i><i></i></span></dd>
					<dd><b>发货：</b><span><i></i><i></i><i></i><i></i><i></i></span></dd>
					<dd><b>物流：</b><span><i></i><i></i><i></i><i></i><i></i></span></dd>
				</dl>
				 
			</div>
			<textarea></textarea>
			<button>发表评价</button>
		</form>
	</div>
</div>

    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

import top from '../index/top'
export default {
	components:{top},

data () {
    return {
			txt:'商品评价'

}
  },
  mounted(){
       $(function(){
		var top_h=$(".vip_top_box").height()
		$(".top_zhanwei_box").css("height",top_h)
	});
					$(function(){
						var n=0;
						$(".user_comment_pinj .pingfen dd i").mousedown(
							function(){
								n=$(this).index()
								if ($(this).css("overflow")=="hidden") {
									$(this).css("background-position-y","bottom")
									$(this).prevAll().css("background-position-y","bottom")
									$(this).prevAll().css("overflow","auto")
								}else{
									$(this).css("background-position-y","top")
									$(this).nextAll().css("background-position-y","top")
									$(this).nextAll().css("overflow","hidden")
								}
							}
						)
					})
      
  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_vip.css');


</style>



