		<ul>
			<li class="cur cur50">
				<h2><router-link to="">①</router-link><span>入驻须知</span></h2>
			</li><!-- 去内联间隙
		 --><li class="cur cur50">
				<i></i><h2><router-link to="">②</router-link><span>公司信息认证</span></h2>
			</li><!-- 去内联间隙
		 --><li class="cur cur50">
