import buttom from '../index/buttom.vue'
import top from '../index/top.vue'
export default {
  components:{top,
  buttom,
  },

data () {
    return {
			txt:'入驻申请'

}
  },
  mounted(){

  }
}
</script>

<style scoped>

@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');

@import url('../../assets/css/news_login_apply.css');

.bgwh a{
display: block;
text-align: center;
height: 50px;
line-height: 50px;
font-size: 30px;
font-family: '宋体';
border: 3px double #ccc;
margin-top: 50px;
background:#eee;
}


</style>



