<template>
  <div id="app">


      
<top :txt="txt"></top>
 
<div class="top_zhanwei_box"></div>	
 
<!-- 内容框 -->
<div class="zy_module-content">	
	<div class="vip_user_recharge_txt bgwh mb6 p_2">
		<div class="txt">
			<p>1）充值成为商城会员的最低门槛200元；</p>
			<p>2）会员等级划分：<br/>
				充值200~500元成为颜文明初级会员，购买商品结算时享受9.8折的优惠；<br/>
				充值500~1000元成为颜文明高级会员，购买商品结算时享受9.5折的优惠；<br/>
				充值1000~2000元成为颜文明铂金会员，购买商品结算时享受9折优惠；<br/>
				充值2000元以上成为颜文明钻石会员，购买商品结算时享受8.8折优惠。<br/>
			</p>
