    <div class="yh_select bgwh mb6">
		<div class="index_title rel">
			<h1>
				<router-link to="">热销品类</router-link>
			</h1>
			<i></i>
		</div>
		<div class="yh_goodsbox fix">
			<div class="l">
				<div class="swiper-container yh_wrapper">
					<ul class="swiper-wrapper">
						<li class="swiper-slide" v-for="(item,index) in rexiao" :key="index">
							<router-link  :to="item.url">
								<img v-lazy="item.img">
