            new:199.00,
            jiu:399.00
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/pic03.jpg',
            txt:'香港颜文明 男式牛皮经典网球运动鞋时尚复古休闲鞋 1517104正品',
            new:199.00,
            jiu:399.00
        },],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
      //热销产品的swiper
            new Swiper('.yh_wrapper', {
					        pagination:{
										el:'.sp02'
									},
					        paginationClickable: true,
					        autoplay: {
                            delay: 2000,
                            stopOnLastSlide: false,
                             disableOnInteraction: true,
                             },
					        loop:"ture",
					        speed:500,
					        autoplayDisableOnInteraction : false,
                        });
