<template>
  <div id="app">
   
   <!-- 品牌专区 -->
    <div class="hot_goods good_stores bgwh mb6">
		<div class="index_title rel">
			<h1>
				<router-link to="">品牌专区</router-link>
			</h1>
			<i></i>
		</div>
		<div class="hot_goods_pic good_stores_pic">
	        <ul class="swiper-wrapper">
	            <li class="swiper-slide" v-for="(item,index) in pins" :key="index">
	            	<router-link :to="item.url">
	            		<i><img v-lazy="item.img"></i>
	            		<p>{{item.txt}}</p>
	            	</router-link>
	            </li>
	            
	        </ul>
	    </div>
    </div>

  </div>
</template>

<script>
import Swiper from 'swiper'

//品牌推荐

export default {
  name: 'app',
  data () {
    return {
        
        pins:[{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo1.jpg',
            txt:'颜文明体育用品'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo2.jpg',
            txt:'耐克NIKE体育用品'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo3.jpg',
            txt:'阿迪达斯体育用品'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo4.jpg',
            txt:'李宁体育用品'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo5.jpg',
            txt:'彪马体育用品'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo6.jpg',
            txt:'颜文明体育用品'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/sj_logo1.jpg',
            txt:'颜文明体育用品'
        }],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
        new Swiper('.good_stores_pic', {
	        slidesPerView: 3.3,
	        paginationClickable: true,
	        spaceBetween: 10,
	        autoplay:{
                delay:2000
            },
	        autoplayDisableOnInteraction : false,
	    });
  }
}
</script>

<style>
</style>



