<template>
  <div id="app">
      <!-- 轮播图 -->
      <div class="swiper-container banner">
		<ul class="swiper-wrapper">
			<li class="swiper-slide" v-for="(item,index) in luns" :key="index">
				<img :src="item">
			</li>
		</ul>
		<div class="swiper-pagination sp01"></div>
		
    </div>

  </div>
</template>

<script>
//轮播图
// import lun1 from '../../assets/images/banner.jpg'
// import lun2 from '../../assets/images/banner01.jpg'


import $ from 'jquery'
import Swiper from 'swiper'



export default {

data () {
    return {
//轮播图素材
        luns:[
             'http://ydty.oss-cn-shanghai.aliyuncs.com/images/banner.jpg',
             'http://ydty.oss-cn-shanghai.aliyuncs.com/images/banner01.jpg',
             'http://ydty.oss-cn-shanghai.aliyuncs.com/images/banner.jpg',
             'http://ydty.oss-cn-shanghai.aliyuncs.com/images/banner01.jpg',
              
              ],

}
  },
  mounted(){
      //轮播图的swiper
      new Swiper('.banner', {
                effect : 'fade',
		        pagination: {
              el:'.sp01'
            },
		        paginationClickable: true,
		        autoplay: {
                delay: 2000,
                stopOnLastSlide: false,
                disableOnInteraction: false,
                },
		        loop:"ture",
		        speed:500,
		        autoplayDisableOnInteraction : false,
            });
  }
}
</script>

<style scoped>
</style>



