			<div class="one" href="javascript:" onclick="history.back();"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/back_jt_w1.png"></div>
			<h1>商家管理中心</h1>
			<div class="r">
	    	<span><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_menu.png"></span>
	    	<dl>
	    		<dt></dt>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon01.png"></i><router-link to="index">首页</router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon02.png"></i><router-link to="feilei">分类</router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon03.png"></i><router-link to="gouwuche">购物车</router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon04.png"></i><router-link to="my">会员中心</router-link></dd>
	    	</dl>
	    </div>
            
		</div>
	</header>	

	<!-- 内容框 -->
	<div class="zy_module-content">
		<div class="login_box">
			<div class="logo"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/logo02.png"></div>
			<form>
				<div>
					<label><router-link to=""><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_user.png"></router-link><input type="text" name="" placeholder="请输入用户名"></label>
				</div>
				<div>
					<label><router-link to=""><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_mm.png"></router-link><input type="password" name="" placeholder="请输入密码"></label>
				</div>
				<div class="yzm">
					<label><router-link to=""><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_mm.png"></router-link><input type="password" name="" placeholder="请输入验证码"></label>
					<span><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/yzm.png"></span>
				</div>
				<button class="zy_login_btn">登 录</button>
				<h2>
