<template>
  <div id="app">

      <!--头部-->
<heanr></heanr>



<div class="module-content">

<!--banner图-->
    <lun></lun>


    
    <!-- 图标导航icon -->
    <navs></navs>

    
    <!-- 商城公告和商家入驻口 -->
    <gonggao></gonggao>


    
    <!-- 热销品类 -->
    <rexiao></rexiao>

    <!-- 广告条 -->
    <guangao></guangao>
    
    <!-- 新品推荐 -->
    <xin></xin>


    
	<!-- 品牌专区 -->
    <pin></pin>
<!-- 广告条 -->
    <guangao></guangao>
    
    <!-- 经典男鞋 -->
<nanxie></nanxie>

<!-- 经典男装 -->
<nanzhaung></nanzhaung>


<!-- 系列分类 -->
<xilie></xilie>

<!-- 配件专区 -->
<peijian></peijian>

<!-- 广告条 -->
    <guangao></guangao>
    
<!-- 猜你喜欢 -->
<cai></cai>

<!-- 返回头部 -->
<totop></totop>

<!-- 底部yi -->
<buttom></buttom>

<!-- 底部导航 -->

<buttom_nav></buttom_nav>







</div>
<!-- 搜索框 -->
<sousuo></sousuo>



  </div>
</template>

<script>
//图片路径



import Swiper from 'swiper'
import $ from 'jquery'
import '../../node_modules/swiper/dist/js/swiper.min.js'

import heanr from './index/heanr.vue'
import lun from './index/lun.vue'
import navs from './index/nav.vue'
import gonggao from './index/gonggao.vue'
import rexiao from './index/rexiao.vue'
import guangao from './index/guangao.vue'
import xin from './index/xin.vue'
import pin from './index/pin.vue'
import nanxie from './index/nanxie.vue'
import nanzhaung from './index/nanzhaung.vue'
import xilie from './index/xilie.vue'
import peijian from './index/peijian.vue'
import cai from './index/cai.vue'
import totop from './index/totop.vue'
import buttom from './index/buttom.vue'
import buttom_nav from './index/buttom_nav.vue'
import sousuo from './index/sousuo.vue'

export default {
    components:{
      //头部部分组件
      heanr,
      //轮播图部分组件
      lun,
      //上面导航部分组件
      navs,
      //公告部分组件
      gonggao,
      //热销部分组件
      rexiao,
      //广告部分组件
      guangao,
      //新品推荐部分组件
      xin,
      //品牌推荐部分组件
      pin,
      //男/女鞋部分组件
      nanxie,
      //男/女装部分组件
      nanzhaung,
      //三系列部分组件
      xilie,
      //配件部分组件
      peijian,
      //猜你喜欢部分组件
      cai,
      //返回头部部分组件
      totop,
      //底部部分组件
      buttom,
      //底部导航部分组件
      buttom_nav,
      //收缩页面部分组件
      sousuo
      },
  data () {
    return {
       
    }
  },
  mounted(){

            $(function(){
		$(".search-box").mousedown(
			function(){
				$(".search_bomb_box").show()
				$(".module-layer").hide()			
				$(".module-content").hide()
			}
		)
		$(".search_bomb_box .search_top>a").mousedown(
			function(){
				$(".search_bomb_box").hide()
				$(".module-layer").show()		
				$(".module-content").show()
			}
		)
	})
      
       

           





   


                    //<!-- 设置图片宽高相等，保持布局统一性 -->
        	$(function(){
        		var w=$(".nine_goods_pic ul li img").width()
        		$(".nine_goods_pic ul li img").css("height",w)
            })
            
            $(function(){
	        		var w=$(".good_stores_pic ul li img").width()
	        		var h=w*2/3
	        		$(".good_stores_pic ul li img").css("height",h)
	        		
                })
                
              
    


           
                
  }
}
</script>

<style scoped>
@import url('../assets/css/index.css');
@import url('../assets/css/public/public_lib.css');



.nav_iconbox{

    overflow: hidden;
    background-color: #fff;
    padding: .3rem 0 .4rem;
    margin-bottom: 6px;
}

</style>



