<template>
  <div id="app">
    
     <!-- 广告条 -->
    <div class="ad_picbox">
		<ul class="swiper-wrapper">
			<li class="swiper-slide" v-for="(item,index) in guang" :key="index">
				<router-link :to="item.url">
                    <img :src="item.img">
                    </router-link>
			</li>
		</ul>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper'

export default {
  name: 'app',
  data () {
    return {
        guang:[{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/ad_pic01.jpg'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/ad_pic02.jpg'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/ad_pic03.jpg'
        }],
        
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
      
            //广告条的swiper
            new Swiper('.ad_picbox', {
		        autoplay:{
                   delay:3000
                },
		        effect:"fade",
		        loop:"ture",
		        speed:700,
		        autoplayDisableOnInteraction : false,
		    });
  }
}
</script>

<style>
</style>



