<template>
  <div id="app">


      
<top :txt="txt"></top>
<div class="top_zhanwei_box"></div>	

<!-- 内容框 -->

<div class="zy_module-content">	
	<div class="vip_user_info_mima">
		<form>
			<div class="jiben">
                <div><span>原密码</span><input type="password" name="" placeholder="请输入原密码"></div>
                <div><span>新密码</span><input type="password" name="" placeholder="请输入新密码"></div>
                <div><span>确认密码</span><input type="password" name="" placeholder="请再次输入新密码"></div>
                <button>确认修改</button>
            </div>
		</form>
	</div>
</div>
    
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

import top from '../index/top.vue'
export default {
	components:{top},

data () {
    return {
			txt:'账户安全'

}
  },
  mounted(){

     
    $(function(){
		var top_h=$(".vip_top_box").height()
		$(".top_zhanwei_box").css("height",top_h)
	})
     

  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_vip.css');


</style>



