<template>
  <div id="app">


<header class="zy_search_top_box">
	<div class="zy_search_top fix">
		<div class="one" href="javascript:" onclick="history.back();"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png"></div>
		<div class="box">
			<span><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/icon_search.png"/></span>
		    <input type="text" placeholder="新品上市" value="" />
	    </div>
	    <div class="r shaix">
	    	<span>筛选</span>
	    </div>
	</div>
</header>

<!-- 筛选框 -->
<div class="zy_shaixuanbox">
	<div class="zy_title_top zy_shaix_box fix">
		<div class="one"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png"></div>
		<h1>筛选</h1>
	</div>
	<div class="sx_con">
		<form>
			<div class="conbox">
				<h2>类别</h2>
				<select>
					<option>经典鞋</option>
					<option>运动鞋</option>
					<option>其他东西</option>
				</select>
				<select>
					<option>经典男鞋</option>
					<option>经典女鞋</option>
				</select>
			</div>
			<div class="conbox">
				<h2>品牌</h2>
				<select>
					<option>不限</option>
					<option>颜文明</option>
					<option>其他品牌</option>
				</select>
			</div>
			<div class="conbox sexbox">
				<h2>性别</h2>
				<label><input type="radio" name="sex" checked="checked">男</label>
				<label><input type="radio" name="sex">女</label>
			</div>
			<div class="conbox price">
				<h2>价格</h2>
				<label><input type="text" name="" value="100">~<input type="text" name="" value="400">元</label>
			</div>
			<div class="conbox sexbox other">
				<h2>其他条件</h2>
				<label><input type="radio" name="qita" checked="checked">不限</label>
				<label><input type="radio" name="qita">网站自营</label>
				<label><input type="radio" name="qita">入驻商家</label>
			</div>
			<div class="btn">
				<button class="quex">取消</button>
				<button>完成</button>
			</div>
		</form>
	</div>
</div>
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'

export default {

data () {
    return {

}
  },
  mounted(){

      $(function(){
		$(".zy_search_top .shaix").mousedown(
			function(){
				$(".zy_shaixuanbox").show()
				$(".zy_search_top_box").hide()			
				$(".zy_module-content").hide()
			}
		)
		$(".zy_shaixuanbox .one").mousedown(
			function(){
				$(".zy_shaixuanbox").hide()
				$(".zy_search_top_box").show()		
				$(".zy_module-content").show()
			}
		)

		var h1=$(".zy_search_top_box").height();
		var h2=$(".zy_rank_box").innerHeight();

		$(window).scroll(function(){
			var s = $(document).scrollTop();
			if (s>h1) {
				$(".zy_rank_box").addClass('scroll_fix')
				$(".zy_gzttc_box").css("padding-top",h2)
			}else{
				$(".zy_rank_box").removeClass('scroll_fix')
				$(".zy_gzttc_box").css("padding-top",0)
			}
		});
	})

  }
}
</script>

<style scoped>

</style>



