<template>
  <div id="app">


      
<top :txt="txt"></top>
<div class="top_zhanwei_box"></div>	

<!-- 内容框 -->

<div class="zy_module-content">	
	<div class="vip_user_info">
		<div class="head bgwh mb6 bbd">
			<router-link to="user_info_person">
				<i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/pic01.jpg"></i>
				<dl>
					<dt>剁手党的春天</dt>
					<dd>钻石会员</dd>
				</dl>
			</router-link>
			<em></em>
		</div>
		<ul class="bgwh">
			<li><router-link to="user_info_mima">账户安全</router-link><em></em></li>
		</ul>
		<div class="exit">
			<router-link to="">退出当前账户</router-link>
		</div>
	</div>
</div>
    
    
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

import top from '../index/top.vue'
export default {
	components:{top},

data () {
    return {
			txt:'设置'

}
  },
  mounted(){
     
            $(function(){
		var top_h=$(".vip_top_box").height()
		$(".top_zhanwei_box").css("height",top_h)
	})

  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_vip.css');


</style>



