<template>
  <div id="app">
    <!-- 底部导航 -->
	<div class="foot_menu">
		<ul>
			<li  v-for="(item,index) in navs" :key="index">
				<router-link :to="item.url">
					<i><img v-lazy="item.img"></i>
					<span>{{item.txt}}</span>
                    <em v-if="item.em!=0">{{item.em}}</em>
				</router-link>
			</li>
		</ul>
	</div>
  </div>
</template>

<script>
import $ from 'jquery'
// import img1 from '../../assets/images/menu_icon01.png'
// import img2 from '../../assets/images/menu_icon02.png'
// import img3 from '../../assets/images/menu_icon03.png'
// import img4 from '../../assets/images/menu_icon04.png'
export default {
  name: 'app',
  data () {
    return {

        navs:[{
            url:'index',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon01.png',
            txt:'首  页',
            em:0
        },{
            url:'fenlei',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon02.png',
            txt:'分  类',
            em:0
        },{
            url:'gouwuche',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon03.png',
            txt:'购物车',
            em:3
        },{
            url:'my',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon04.png',
            txt:'我 的',
            em:0
        },]

}
  },
  mounted(){

     $(function(){
         $(".foot_menu ul li").eq(0).addClass('cur');
         $(".foot_menu ul li").eq(2).addClass('gwc');
     })

  }
}
</script>

<style>
</style>



