<template>
  <div id="app">
    
    <!-- 图标导航icon -->
    <div class="swiper-container nav_iconbox">
	   	<div class="swiper-wrapper">
	   		<!-- 每个ul有8个li，不能多于8个 -->
			<ul class="swiper-slide"> 
		    	<li v-for="(item,index) in nav1s" :key="index">
                    <router-link :to="item.url">
                        <img v-lazy="item.img">
		    			<span>{{item.name}}</span>
                    </router-link>
		    	</li>
		    </ul>
		    <ul class="swiper-slide">
		    	<li v-for="(item,index) in nav2s" :key="index">
                    <router-link :to="item.url">
                        <img v-lazy="item.img">
		    			<span>{{item.name}}</span>
                    </router-link>
		    	</li>
		    </ul>
		</div>
		<div class="swiper-pagination icon01"></div>
		
    </div>


  </div>
</template>

<script>
import Swiper from 'swiper'

//导航栏

export default {
  data () {
    return {
        //轮播图下面nav
        nav1s:[{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon01.jpg',
            name:'经典男鞋'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon02.jpg',
            name:'女鞋'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon03.jpg',
            name:'T恤'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon04.jpg',
            name:'外套/卫衣'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon05.jpg',
            name:'夏季新款'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon06.jpg',
            name:'户外箱包'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon07.jpg',
            name:'体育器材'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon08.jpg',
            name:'积分专区'
        }],
        nav2s:[{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon04.jpg',
            name:'其他栏目'
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/nav_icon07.jpg',
            name:'健身器材'
        }],
    }
  },
  mounted(){
        new Swiper('.nav_iconbox', {
           pagination: {
            el: '.icon01',
            },
		        paginationClickable: true,
		        speed:500,
		        autoplayDisableOnInteraction : false,
            });

  }
}
</script>

<style>
</style>



