<template>
  <div id="app">
      <div class="zy_login_box">
          <!-- 头部 -->
          <header class="zy_search_top_box">
		<div class="zy_title_top fix">
			<div class="one" href="javascript:" onclick="history.back();">
                <img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png"></div>
			<h1>会员注册</h1>
			<router-link to="login">账户登录</router-link>
		</div>
	</header>	

    
	<!-- 内容框 -->
	<div class="zy_module-content">
		<div class="login_box zhuce_box">
			<div class="zhuce_style fix">
				<router-link to="">手机注册</router-link>
				<router-link to="">邮箱注册</router-link>
				<i></i>
			</div>
			
			<div class="zhuce_con">
				<div class="zhuce_tel">
					<form>
						<div v-for="(item,index) in phone" :key="index">
							<label>
                                <router-link :to="item.url">
                                    <img :src="item.img">
                                </router-link>
                            <input type="text" name="" :placeholder="item.txt">
                        </label>
                        <span v-if="item.span!=''">{{item.span}}</span>
						</div>
						
						<h2>
							<label><input type="checkbox" name="">我已看过并接受<router-link to="" >《用户协议》</router-link></label>
						</h2>
						<button class="zy_login_btn">注 册</button>
					</form>
				</div>
				<div class="zhuce_msg">
					<form>
						<div v-for="(item,index) in you" :key="index">
							<label>
                                <router-link :to="item.url">
                                    <img :src="item.img">
                                </router-link>
                            <input type="text" name="" :placeholder="item.txt">
                        </label>
                        <span v-if="item.span!=''">{{item.span}}</span>
						</div>
						<h2>
							<label><input type="checkbox" name="">我已看过并接受<router-link to="" >《用户协议》</router-link></label>
						</h2>
						<button class="zy_login_btn">注 册</button>
					</form>
				</div>
			</div>			
		</div>

		<!-- 底部 -->
		<buttom></buttom>
				
	</div>


    
    <buttom_nav></buttom_nav>
      </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'




import buttom from '../index/buttom.vue'
import buttom_nav from '../index/buttom_nav.vue'

export default {
  components:{buttom_nav,buttom},
  data () {
    return {
        phone:[{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_iphone.png',
            txt:'请填写手机号',
            span:''
        },{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_mm.png',
            txt:'请输入密码',
            span:''
        },{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_mm.png',
            txt:'请确认密码',
            span:''
        },{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_iphone.png',
            txt:'手机验证码',
            span:'获取手机验证码'
        },
        
        ],
        you:[{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_msg.png',
            txt:'请填写邮箱',
            span:''
        },{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_mm.png',
            txt:'请输入密码',
            span:''
        },{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_mm.png',
            txt:'请确认密码',
            span:''
        },{
            url:'',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_msg.png',
            txt:'邮箱验证码',
            span:'获取邮箱验证码'
        },
        
        ],

    }
  },mounted(){
      $(function(){
          
          $(".zhuce_tel div").eq(3).addClass('yzm')
          $(".zhuce_msg div").eq(3).addClass('yzm')
					$(".zy_login_box .zhuce_con>div").hide().first().show()
					var n=0;
					var w=$(".zy_login_box .zhuce_style i").width()
					$(".zy_login_box .zhuce_style a").mousedown(
						function(){
							n=$(this).index()
							$(".zy_login_box .zhuce_con>div").hide().eq(n).fadeIn()
							$(".zy_login_box .zhuce_style i").animate({left:n*w},300)
							$(this).css("color","#10805a")
							$(this).siblings().css("color","#333")
						}
					)
                });
        
  }
}
</script>

<style scoped>

@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');

@import url('../../assets/css/news_login_apply.css');




</style>



