<template>
  <div id="app">


      <div class="foot_menu zy_supplier_menu">
		<ul>
			<li v-for="(item,index) in navs" :key="index">
				<router-link :to="item.url">
					<i><img :src="item.img"></i>
					<span>{{item.txt}}</span>
				</router-link>
			</li>
		 
		</ul>
	</div>
	<div class="da">
		<div class="con">
			<div class="yi">暂不提供联系服务！</div>
			<div class="er">确认</div>
		</div>
	</div>


      
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

 

export default {
	 
data () {
    return {
		 navs:[{
			 url:'supplier_catalog',
			 img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon02.png',
			 txt:'店铺分类'
		 },{
			 url:'supplier_introduce',
			 img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon01.png',
			 txt:'店铺详情'
		 },{
			 url:'',
			 img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon04.png',
			 txt:'联系我们'
		 },]


}
  },
  mounted(){

		$(function(){

			$(".zy_supplier_menu li").eq(2).click(function(){
				$(".da").css('display','block')
			})
			$(".er").click(function(){
				$(".da").css('display','none')
			})
			


		})
       

  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_main.css');


.da{
	display: none;
	position: fixed;
	top: 0;
	left:0;
	width: 100%;
	height: 100%;
	background-color: rgba(255, 255, 255, 0.8);
	color: black;
font-family: '宋体'
}

.da .con{
	position: fixed;
	top: 50%;
	left:0;
	width: 100%;
	height: 150px;
	margin-top: -75px;
text-align: center;
}

.yi{ 
	line-height: 50px;
	height: 50px;
	font-size: 30px;
	margin-bottom: 20px;
}
.er{
	width: 100px;
	height: 50px;
	font-size: 30px;
	border-radius: 10px;
	background:rgba(0, 0, 255, 0.5);
	line-height: 50px;
	text-align: center;
	margin: 0 auto;
	
}

</style>



